header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8vw;
  padding: 0 5vw;
}
header .logo {
  padding: 20px;
}
header .logo img {
  width: 60px;
  height: 60px;
}
header nav.main-nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
header nav.main-nav ul li a {
  padding: 10px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 13px;
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: relative;
  top: 0;
  cursor: pointer;
  transition: all 0.3s 0.1s ease;
}
header nav.main-nav ul li a:hover {
  color: #ffbb00;
  top: -3px;
}
header .right {
  display: flex;
  align-items: center;
  gap: 30px;
  border: none;
}
header .right .vote select {
  padding: 50px;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 10px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  cursor: pointer;
}
header .right .vote select option {
  background-color: #343434;
}
header .right .line {
  width: 1px;
  height: 50px;
  background-color: #ffffff6b;
}
header .right .socials {
  display: flex;
  gap: 20px;
}
header .right .socials a {
  position: relative;
  top: 0;
  transition: all 0.3s 0.1s ease;
}
header .right .socials a:hover {
  top: -3px;
}
header .right .socials .open-menu {
  display: none;
}
header .menu-hidden {
  display: none;
}
@media screen and (max-width: 1040px) {
  .socials a {
    display: none;
  }
}
@media screen and (max-width: 860px) {
  nav.main-nav {
    display: none;
  }
  header .open-menu#open-menu {
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
    align-items: flex-end;
  }
  header .open-menu#open-menu .line {
    width: 23.5px;
    height: 2.5px;
    flex-shrink: 0;
    border-radius: 3px;
    background: #FFF;
  }
  header .open-menu#open-menu .line3 {
    width: 16px;
  }
  .menu-hidden#menu-hidden {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 60vh;
    animation: Menu ease 0.5s;
    position: fixed;
    border-radius: 0 0 30px 30px;
    top: 0;
    background-color: #1b1b1b;
    z-index: 500;
    margin: 0;
    right: 0px;
  }
  .menu-hidden#menu-hidden .open-menu {
    position: absolute;
    top: 50px;
    right: 20px;
  }
  .menu-hidden#menu-hidden nav#main-nav ul {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
  }
  .menu-hidden#menu-hidden nav#main-nav ul li a {
    padding: 10px;
    color: #FFF;
    font-family: Open Sans;
    font-size: 13px;
    text-decoration: none;
    font-weight: 700;
    letter-spacing: 1.2px;
    text-transform: uppercase;
    position: relative;
    top: 0;
    transition: all 0.3s 0.1s ease;
  }
  .menu-hidden#menu-hidden nav#main-nav ul li a:hover {
    color: #ffbb00;
    top: -3px;
  }
  .menu-hidden#menu-hidden #socials {
    display: flex;
    gap: 20px;
  }
  .menu-hidden#menu-hidden #socials a {
    position: relative;
    top: 0;
    transition: all 0.3s 0.1s ease;
  }
  .menu-hidden#menu-hidden #socials a:hover {
    top: -3px;
  }
}
@keyframes Menu {
  from {
    top: -100px;
    opacity: 0;
  }
  to {
    top: 0px;
    opacity: 1;
  }
}
