.bg-img1 {
  background-image: url('../images/bg-img1.1.png');
}
.bg-img2 {
  background-image: url('../images/bg-img1.png');
}
.bg-img {
  background-repeat: repeat-x;
  background-color: #08090A;
  background-size: cover;
  position: absolute;
  z-index: -1;
  width: 100%;
  left: 0;
  right: 0;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
}
.dim {
  background: radial-gradient(189.96% 61.18% at 50% 38.82%, rgba(255, 181, 72, 0) 0%, rgba(52, 44, 29, 0.8) 73.22%, #08090A 99.74%);
}
