.section2 {
  background-image: url('../images/bg3.png');
  background-size: cover;
  backdrop-filter: opacity(0.5);
  background-repeat: repeat-x;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  align-items: center;
  opacity: 1;
}
.section2 .gradient {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: radial-gradient(81.01% 53.57% at 30.85% 53.65%, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0) 19.2%, rgba(4, 5, 6, 0.52) 47.48%, #080A0B 99.74%);
}
.section2 .content {
  margin-left: 50vw;
  position: relative;
  z-index: 2;
}
.section2 .content h3,
.section2 .content h1 {
  text-align: left;
}
.section2 .content h1 {
  margin-bottom: 50px;
}
.section2 .content .elements-info .element {
  margin-bottom: 30px;
}
.section2 .content .elements-info .element .open-info {
  display: flex;
  gap: 10px;
  align-items: center;
  cursor: pointer;
}
.section2 .content .elements-info .element .open-info h2 {
  color: #FFB548;
  text-align: center;
  font-family: Bebas Neue;
  font-size: 36px;
  font-weight: 500;
  letter-spacing: 2px;
}
.section2 .content .elements-info .element p.hidden {
  color: #FFF;
  text-align: justify;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  width: 400px;
  margin-left: 27px;
  animation: open 0.5s ease;
  position: relative;
  top: 0;
  height: 80px;
}
@keyframes open {
  from {
    height: 0px;
    opacity: 0;
    top: -20px;
  }
  to {
    height: 80px;
    top: 0;
    opacity: 1;
  }
}
@media screen and (max-width: 640px) {
  .content#content {
    margin-left: 20px;
  }
  .content#content .elements-info .element p.hidden {
    width: calc(90vw - 27px);
  }
}
