.section5 {
  background: url(../images/bg6.png);
  background-size: cover;
  background-repeat: repeat-x;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
}
.section5 .content {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  height: 120vh;
  width: 86vw;
  position: relative;
  z-index: 2;
}
.section5 .content .right {
  margin-left: 35vw;
  width: 50vw;
}
.section5 .content .right h1,
.section5 .content .right h3 {
  text-align: left;
}
.section5 .content .right p.descript {
  margin-top: 20px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  width: 45vw;
}
.section5 .content .right .bottom {
  margin-top: 50px;
  display: flex;
  width: 50vw;
  gap: 15px;
  align-items: center;
}
.section5 .content .right .bottom .input-email {
  width: 320px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.08);
  padding: 2px 20px;
  height: 60px;
}
.section5 .content .right .bottom .input-email p {
  color: #FFF;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  opacity: 0.5;
}
.section5 .content .right .bottom .input-email input {
  outline: none;
  background-color: transparent;
  border: none;
  color: #FFB548;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  width: 100%;
}
.section5 .content .right .bottom button {
  height: 60px;
  margin: 0;
}
@media screen and (max-width: 1046px) {
  .section5#section5 .content {
    width: 95vw;
  }
  .section5#section5 .content .right {
    width: 70vw;
    margin-left: 30vw;
  }
}
@media screen and (max-width: 642px) {
  .section5#section5 .content {
    width: 95vw;
  }
  .section5#section5 .content .right {
    filter: drop-shadow(5px 5px 5px black);
    width: 95vw;
    margin-left: 2vw;
  }
  .section5#section5 .content .right p.descript {
    width: 90vw;
  }
  .section5#section5 .content .right .bottom {
    width: 90vw;
  }
}
