body {
  margin: 0;
  padding: 0;
  background-color:#08090A;
}
@font-face {
  font-family: Bebas-Cyrillic;
  src: url(./fonts/Bebas_Cicrilic.ttf);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
*::selection {
  color: #ffbb00;
  background-color: #ffffff1b;
}

h1 {
  color: #FFF;
  text-align: center;
  font-family: Bebas Neue;
  font-size: 6vw;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 3px;
  text-transform: uppercase;
}
h1.ru-h1 {
  font-family: Bebas-Cyrillic;
}
h3 {
  color: #FFF;
  font-family: Bebas Neue;
  text-align: center;
  font-size: 2vw;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 2px;
}
h3.ru-h3 {
  font-family: Bebas-Cyrillic;
}
h2 {
  color: #FFB548;
  text-align: center;
  font-family: Bebas Neue;
  font-size: 36px;
  font-weight: 500;
  letter-spacing: 2px;
}
h2#ru-h2 {
  font-family: Bebas-Cyrillic;
}
@media screen and (max-width: 780px) {
  h1 {
    font-size: 10vw;
  }
  h3 {
    font-size: 5vw;
  }
  
}
@media screen and (max-width: 750px) {
  h1 {
    font-size: 12vw;
  }
  h3 {
    font-size: 6vw;
  }
  
}
button.buy {          
  margin-top: 30px;
  border-radius: 4px;
  background: linear-gradient(181deg, #FFB548 0%, #F3AF34 99.52%);
  border: none;
  display: flex;
  align-items: center;
  gap: 10px;  
  padding: 20px 16px;
  cursor: pointer; 
  transition: all 0.3s ease;
  position: relative;
  top: 0;
}
button.buy .line {
  background-color: #1a191777;
  width: 1px;
  height: 24px;
}  
button.buy p {
  color: #1A1917;
  text-align: center;
  font-family: Open Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;    
}   
button.buy:hover {
  top: -3px;
}   