.section3 {
  background-image: url('../images/bg4.png');
  background-size: cover;
  background-repeat: repeat-x;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  opacity: 1;
}
.section3 .gradient {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: radial-gradient(81.01% 53.57% at 30.85% 53.65%, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0) 19.2%, rgba(4, 5, 6, 0.52) 47.48%, #080A0B 99.74%);
}
.section3 .content {
  width: 100vw;
  margin-top: 100px;
  position: relative;
  z-index: 2;
}
.section3 .content h1 {
  margin-bottom: 50px;
}
.section3 .content .table {
  width: 50vw;
}
.section3 .content .table table {
  border-collapse: collapse;
  margin-left: 10vw;
}
.section3 .content .table table tr td {
  border: 1px solid #9797974f;
  padding-left: 18px;
  min-height: fit-content;
  height: 150px;
}
.section3 .content .table table tr td h2 {
  text-align: left;
  margin-bottom: 7px;
}
.section3 .content .table table tr td p {
  color: #FFF;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  /* 122.222% */
}
@media screen and (max-width: 700px) {
  .section3#section3 {
    justify-content: center;
  }
  .section3#section3 .table {
    width: 90vw;
  }
  .section3#section3 .table table {
    margin-left: 0;
  }
}
