footer div.lvl1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 8vw;
  padding: 0 5vw;
}
footer div.lvl1 .logo {
  padding: 20px;
}
footer div.lvl1 .logo img {
  width: 60px;
  height: 60px;
}
footer div.lvl1 nav.footer-nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
footer div.lvl1 nav.footer-nav ul li a {
  padding: 10px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 13px;
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: relative;
  top: 0;
  cursor: pointer;
  transition: all 0.3s 0.1s ease;
}
footer div.lvl1 nav.footer-nav ul li a:hover {
  color: #ffbb00;
  top: -3px;
}
footer div.lvl1 svg {
  cursor: pointer;
  margin-bottom: 10px;
}
footer div.line {
  width: 90vw;
  height: 1px;
  background-color: #ffffff68;
  margin-left: 5vw;
  margin-right: 5vw;
}
footer div.lvl2 {
  padding-bottom: 10px;
  padding-left: 5vw;
  width: 90vw;
  justify-content: space-between;
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap-reverse;
}
footer div.lvl2 p.copyright {
  color: #FFF;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  opacity: 0.5;
}
footer div.lvl2 p.menu {
  color: #FFF;
  font-family: Open Sans;
  font-size: 13px;
  text-decoration: none;
  font-weight: 700;
  padding-bottom: 20px;
}
footer div.lvl2 p.menu a {
  padding: 10px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 13px;
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: relative;
  top: 0;
  transition: all 0.3s 0.1s ease;
}
footer div.lvl2 p.menu a:hover {
  color: #ffbb00;
  top: -3px;
}
@media screen and (max-width: 540px) {
  footer.footer div.lvl1 {
    justify-content: center;
  }
  footer.footer div.lvl1 nav.footer-nav ul {
    gap: 10px ;
  }
}
