.section1 {
  background-image: url('../images/bg2.png'), radial-gradient(189.96% 61.18% at 50% 38.82%, rgba(255, 181, 72, 0) 0%, rgba(52, 44, 29, 0.8) 73.22%, #08090A 99.74%);
  background-size: cover;
  background-repeat: repeat-x;
  position: relative;
  z-index: 1;
  width: 100%;
  left: 0;
  right: 0;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  align-items: center;
}
.section1 .gradient {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 120vh;
  background: radial-gradient(136.03% 54.57% at 50% 48.04%, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0) 60.63%, #080A0B 99.74%);
}
.section1 .content {
  z-index: 1;
  width: 90vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-left: 5vw;
}
.section1 .content .left {
  width: 35vw;
}
.section1 .content .left h1 {
  text-align: left;
  font-weight: 200;
}
.section1 .content .left h3 {
  text-align: left;
}
.section1 .content .left .horizontal-line {
  background-color: white;
  width: 100%;
  height: 3px;
}
.section1 .content .left .text-descript {
  display: flex;
  justify-content: flex-end;
  margin-top: 30px;
}
.section1 .content .left .text-descript p {
  width: 400px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
}
.section1 .content .right .slider {
  display: flex;
  align-items: center;
}
.section1 .content .right .slider .slides {
  display: flex;
  width: 500px;
  align-items: center;
}
.section1 .content .right .slider .slides img {
  object-fit: cover;
  border-radius: 10px;
  position: relative;
  left: 0;
}
.section1 .content .right .slider .slides img:nth-child(1) {
  z-index: 5;
  width: 450px;
  height: 550px;
  animation: slide1 ease 0.5s;
}
.section1 .content .right .slider .slides img:nth-child(2) {
  z-index: 4;
  left: -400px;
  width: 430px;
  height: 500px;
}
.section1 .content .right .slider .slides img:nth-child(3) {
  border-radius: 5px;
  z-index: 3;
  left: -780px;
  width: 400px;
  height: 450px;
  animation: slide3 ease 0.5s;
}
.section1 .content .right .slider .step {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  position: relative;
  top: -40px;
  z-index: 8;
  left: -30px;
}
.section1 .content .right .slider .swipe {
  cursor: pointer;
  padding: 20px;
  position: relative;
  top: 0;
  z-index: 20;
  transition: all 0.3s ease;
}
.section1 .content .right .slider .swipe:hover {
  top: -3px;
}
@keyframes slide1 {
  from {
    left: -10px;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@keyframes slide3 {
  from {
    left: -770px;
    opacity: 0;
  }
  to {
    left: -780px;
    opacity: 1;
  }
}
@media screen and (max-width: 1016px) {
  .section1 {
    height: fit-content;
    background-repeat: repeat;
  }
  .section1 .content {
    padding-top: 20px;
    width: 98vw;
    justify-content: center;
  }
  .section1 .content .left {
    width: 80%;
    padding-bottom: 20px;
  }
  .section1 .content .left h1 {
    text-align: center;
  }
  .section1 .content .left h3 {
    text-align: center;
  }
  .section1 .content .left .text-descript {
    text-align: center;
    justify-content: center;
  }
  .section1 .content .right .slider .slides {
    display: flex;
    width: 300px;
    align-items: center;
  }
  .section1 .content .right .slider .slides img {
    object-fit: cover;
    border-radius: 10px;
    position: relative;
    left: 0;
  }
  .section1 .content .right .slider .slides img:nth-child(1) {
    width: 280px;
    height: 330px;
    animation: slide1 ease 0.5s;
  }
  .section1 .content .right .slider .slides img:nth-child(2) {
    left: -240px;
    width: 250px;
    height: 300px;
  }
  .section1 .content .right .slider .slides img:nth-child(3) {
    left: -380px;
    width: 150px;
    height: 220px;
    animation: slide3Phone ease 0.5s;
  }
  .section1 .content .right .slider .swipe {
    padding: 20px 0 20px 20px ;
  }
}
@keyframes slide3Phone {
  from {
    left: -370px;
    opacity: 0;
  }
  to {
    left: -380px;
    opacity: 1;
  }
}
