.section4 {
  background-image: url(../images/bg6.png);
  background-size: cover;
  background-repeat: repeat-x;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
}
.section4 .gradient {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: radial-gradient(81.01% 53.57% at 30.85% 53.65%, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0) 19.2%, rgba(4, 5, 6, 0.52) 47.48%, #080A0B 99.74%);
}
.section4 .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 120vh;
  width: 86vw;
  position: relative;
  z-index: 2;
}
.section4 .content .left h3 {
  text-align: left;
}
.section4 .content .left h1 {
  text-align: left;
  margin-bottom: 50px;
}
.section4 .content .left p.descript {
  color: #FFF;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  /* 122.222% */
  width: 400px;
}
.section4 .content .left button {
  margin-top: 50px;
}
.section4 .content .right {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: end;
  align-items: end;
}
.section4 .content .right .quote {
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.08);
  width: 450px;
  padding: 15px;
}
.section4 .content .right .quote .head {
  display: flex;
  justify-content: space-between;
}
.section4 .content .right .quote .head .pro {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 20px;
}
.section4 .content .right .quote .head div.profile {
  width: 65px;
  height: 65px;
  background: #FFB548;
  border-radius: 50%;
}
.section4 .content .right .quote .head div.profile img {
  border-radius: 50%;
  position: relative;
  right: 5px;
  bottom: 5px;
}
.section4 .content .right .quote .head .name h2 {
  text-align: left;
}
.section4 .content .right .quote .head .name p {
  color: #FFF;
  text-align: justify;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 0px;
  /* 122.222% */
  opacity: 0.5;
}
.section4 .content .right .quote q {
  color: #FFF;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 400;
}
.section4 .content .right .quote p.date {
  margin-top: 15px;
  color: #FFF;
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 400;
  opacity: 0.5;
  text-align: end;
}
.section4 .content .right .quote path {
  transition: all 0.3s ease;
  cursor: pointer;
}
.section4 .content .right .quote path:hover {
  scale: 0.9;
}
.section4 .content .right .quote:nth-child(2) {
  margin-right: 7vw;
}
@media screen and (max-width: 1074px) {
  .section4#section4 .content {
    margin-left: 0;
    justify-content: center;
  }
  .section4#section4 .content h1,
  .section4#section4 .content h3 {
    width: 90vw;
    text-align: center;
  }
  .section4#section4 .content .left {
    width: 90vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }
  .section4#section4 .content .right .quote:nth-child(2) {
    margin-right: 0vw;
  }
}
@media screen and (max-width: 455px) {
  .section4#section4 .content .left p.descript {
    width: 90vw;
  }
  .section4#section4 .content .right .quote {
    width: 90vw;
  }
  .section4#section4 .content .right .quote:nth-child(2) {
    margin-right: 0vw;
  }
}
