main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
main div.main-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
main div.main-center button.buy {
  margin-top: 30px;
  border-radius: 4px;
  background: linear-gradient(181deg, #FFB548 0%, #F3AF34 99.52%);
  border: none;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 20px 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  top: 0;
}
main div.main-center button.buy .line {
  background-color: #1a191777;
  width: 1px;
  height: 24px;
}
main div.main-center button.buy p {
  color: #1A1917;
  text-align: center;
  font-family: Open Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
main div.main-center button.buy:hover {
  top: -3px;
}
main a.main-bottom {
  display: flex;
  align-items: center;
  flex-direction: column;
  transition: all 0.3s 0.1s ease;
  text-decoration: none;
  position: relative;
  top: 0;
}
main a.main-bottom:hover {
  top: 3px;
}
main a.main-bottom p {
  color: #FFF;
  text-align: justify;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 22px;
  /* 183.333% */
  text-transform: uppercase;
}
